import React, { useState, useEffect, Suspense } from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import styled, { ThemeProvider } from "styled-components";
import axios from "axios";
import LoadingSpinner, { LoadingContainer } from "./common/loading";
import Helmet from "react-helmet";
import { ToastContainer, toast } from "react-toastify";
import { formatKms } from "./common/helper";
import { beUrl } from "../config";
import { Link } from "react-router-dom";
import { removeStyle } from "../styles/links";

const FullscreenToggle = styled.span`
  position: fixed;
  z-index: 999999999999;
  right: 20px;
  bottom: 20px;
  cursor: pointer;
  font-size: 30px;
  color: white;
  padding: 5px 13px;
  border-radius: 50%;
  text-align: center;
  background-color: ${(props) => props.theme.badge.bg};
  border: 4px solid transparent;

  &:hover {
    background-color: orange;
  }
`;

const SliderContainer = styled.div`
  & img {
    width: inherit;
    background-image: url("/img/pulse.gif");
    background-repeat: no-repeat;
    background-position: center;
  }
`;

const DetailsList = styled.ul`
  list-style-type: none;
  padding-left: 0px;
  color: ${(props) => props.theme.primaryText};
`;

const Price = styled.span`
  background-color: ${(props) => props.theme.badge.bg};
  color: ${(props) => props.theme.badge.fg};
`;

const H1 = styled.h1`
  color: ${(props) => props.theme.primaryText};
`;

const Error404 = styled.div`
  margin: 50px auto;
  color: ${(props) => props.theme.bgColor};
  -webkit-filter: invert(100%);
  filter: invert(100%);
  font-size: 40px;
  text-align: center;

  & span {
    font-size: 1.4em;
    border-bottom: 3px solid red;
  }
`;

const BackToList = styled.h5`
  margin: 20px 0px 20px 20px;
  display: inline-block;
  padding-bottom: 5px;
  &:hover {
    color: orange;
  }
`;

const getPrimaryOptions = (w, h) => {
  const obj = {
    type: "loop",
    perMove: 1,
    gap: "1rem",
    pagination: true,
    lazyLoad: "nearby",
  };

  if (w && h) {
    return {
      ...obj,
      fixedWidth: w,
      fixedHeight: h,
    };
  } else {
    return obj;
  }
};

const getSecondaryOptions = () => {
  return {
    type: "slide",
    rewind: true,
    gap: "1rem",
    pagination: false,
    fixedWidth: 110,
    fixedHeight: 70,
    cover: true,
    focus: "center",
    isNavigation: true,
    updateOnMove: true,
  };
};

function CarDetails(props) {
  const [fullscreen, setFullscreen] = useState(false);
  const [car, setCar] = useState(undefined);

  const [primaryOptions] = useState(() => getPrimaryOptions());
  const [secondaryOptions] = useState(() => getSecondaryOptions());

  const primaryRef = React.createRef();
  const secundaryRef = React.createRef();

  useEffect(() => {
    if (primaryRef.current && secundaryRef.current) {
      primaryRef.current.sync(secundaryRef.current.splide);
    }
  }, [primaryRef, secundaryRef]);

  useEffect(() => {
    async function fetchData() {
      try {
        let { data: c } = await axios.get(
          `${beUrl}/cars/${props.match.params.id}`
        );

        if (!c) {
          c = -1;
        } else {
          document.title = c.title;
        }

        setCar(c);
      } catch {
        setCar(-1);
      }
    }
    fetchData();
  }, [props.match.params.id]);

  const ContactForm = React.lazy(() => import("./common/contact-form"));

  const { id } = props.match.params;

  const getSlider = () => {
    return (
      <SliderContainer>
        <Splide options={primaryOptions} ref={primaryRef}>
          {renderSlides()}
        </Splide>
        <Splide options={secondaryOptions} ref={secundaryRef} className="mt-3">
          {renderSlides()}
        </Splide>
      </SliderContainer>
    );
  };

  const renderSlides = () => {
    const items = [];

    for (let i = 1; i <= car.photos; i++) {
      items.push(
        <SplideSlide key={`${car.id}/${i}`}>
          <img
            src={process.env.PUBLIC_URL + `/img/${car.id}/${i}.webp`}
            alt={"Fotos da viatura"}
          />
        </SplideSlide>
      );
    }

    return items;
  };

  const getBadge = () => {
    return car.sold ? (
      <h1>
        <span className="badge badge-warning">Vendido</span>
      </h1>
    ) : (
      <h1>
        <Price className="badge badge-primary">{car.price}€</Price>
      </h1>
    );
  };

  const toggleFullscreen = () => {
    setFullscreen((prev) => !prev);
  };

  const GetCarSpecs = () => {
    return (
      <table className="table table-hover">
        <tbody>
          {Object.keys(car.attributes).map((key) => {
            return (
              <tr key={key}>
                <th scope="row">{car.attributes[key].description}</th>
                <td>
                  {key === "kms"
                    ? formatKms(car.attributes[key].value)
                    : car.attributes[key].value}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    );
  };

  if (!car) {
    return (
      <LoadingContainer>
        <LoadingSpinner></LoadingSpinner>
      </LoadingContainer>
    );
  }

  if (car === -1) {
    return (
      <ThemeProvider theme={props.theme}>
        <Error404>
          <span>Erro:</span> Viatura não existe: id {id}
        </Error404>
      </ThemeProvider>
    );
  }

  const structuredJSON = () => {
    let data = {
      "@context": "http://schema.org/",
      "@type": "Product",
      name: `${car.title}`,
      image: `https://vinculobjetivo.pt/img/${car.id}/thumbnail.webp`,
      description: `${car.title} | ${car.attributes.kms.value} KMS | ${car.attributes.year.value} | ${car.attributes.fuelType.value}`,
      url: `https://vinculobjetivo.pt/carros`,
      offers: {
        "@type": "Offer",
        priceCurrency: "€",
        price: car.price,
        url: `https://vinculobjetivo.pt/carro/${car.id}`,
        itemCondition: "https://schema.org/UsedCondition",
        availability: `${
          car.sold ? "https://schema.org/SoldOut" : "https://schema.org/InStock"
        }`,
        seller: {
          "@type": "Organization",
          name: "Vinculobjetivo",
        },
      },
      brand: {
        "@type": "Brand",
        name: `${car.attributes.brand.value}`,
      },
    };

    return JSON.stringify(data);
  };

  if (car && car.sold) {
    var options = {
      position: "top-right",
      autoClose: false,
      hideProgressBar: true,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
    };

    toast.error("⚠️ Esta viatura já foi vendida 😢", options);
  }

  return (
    <Suspense
      fallback={
        <LoadingContainer>
          <LoadingSpinner></LoadingSpinner>
        </LoadingContainer>
      }
    >
      <ToastContainer limit={1} />
      <ThemeProvider theme={props.theme}>
        <Helmet>
          <script className="structured-data-list" type="application/ld+json">
            {structuredJSON()}
          </script>
        </Helmet>

        <FullscreenToggle onClick={toggleFullscreen}>
          <i className={fullscreen ? "fas fa-compress" : "fas fa-expand"}></i>
        </FullscreenToggle>
        {!fullscreen ? (
          <div>
            <Link to="/carros" style={{ ...removeStyle }}>
              <BackToList className="underline">
                <i class="fas fa-arrow-left"></i> Catálogo
              </BackToList>
            </Link>
            <div className="row ml-lg-5 mr-lg-5 ml-sm-2 mr-sm-2 ml-1 mr-1">
              <div className="col-12 col-lg-9 ">{getSlider()}</div>
              <div className="col-12 col-lg-3 ">
                <H1>{car.title}</H1>
                {getBadge()}
                <DetailsList>{GetCarSpecs()}</DetailsList>
              </div>
            </div>
            <ContactForm theme={props.theme} subject={car.title}></ContactForm>
          </div>
        ) : (
          getSlider()
        )}
      </ThemeProvider>
    </Suspense>
  );
}

export default CarDetails;
